import { Component, OnInit } from '@angular/core';
import { BusinessFieldManageBean } from './bean/business-field-manage.bean';
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../../common/http/common.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-business-field-manage',
  templateUrl: './business-field-manage.component.html',
  styleUrls: ['./business-field-manage.component.scss'],
  providers: [Commfunc]
})
export class BusinessFieldManageComponent implements OnInit {
  businessFieldManageBean: BusinessFieldManageBean = new BusinessFieldManageBean;
  statusId: any = [];//抵用券状态
  tableData: any = [];
  contentTypeId:Array<any>=[];//字段类型
  
  msgs: Message[] = [];//提示信息
  first: number = 0;//分页控制
  code: any;
  rows: number;//当前页条数
  currentPage: number;//当前页
  headerTitle: string;//模态框标题
  display: boolean;//模态框显示
  updateValue: any;//修改回显内容
  showModel: string;
  total: string;
  constructor(
    public commonHttpService: CommonHttpService,
    public commfunc: Commfunc
  ) {
    this.codeValues()
    this.statusId = this.code["SpContentStatus"];
  }

  ngOnInit() {
    this.businessFieldManageBean.pageNum = 1;
    this.businessFieldManageBean.pageSize = 10;

    //调查询服务
    this.query();
  }

  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //查询
  query() {
    this.commonHttpService.querySpContent(this.businessFieldManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.tableData = data.contentList;
        this.total=data.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //点击查询
  queryClick() {
    this.first = 0;
    this.businessFieldManageBean.pageNum = 1;
    this.query();
  }

  //重置
  resetClick() {
    this.businessFieldManageBean.contentName = '';
    this.businessFieldManageBean.contentTypeId = '';
    this.businessFieldManageBean.statusId = '';
  }

  //新增
  add() {
    this.headerTitle = "业务字段新增";
    this.showModel = "1";
    this.display = true;
  }

  //新增回调
  addCall(param) {
    this.display = param.display;
    if (param.message) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.message })
      this.query();
    }
  }
  update(item) {
    this.headerTitle = "业务字段修改";
    this.showModel = "2";
    this.display = true;
    this.updateValue = item;
  }
  //修改回调
  updateCall(param) {
    console.log(param);
    this.display = param.display;
    if (param.message) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.message })
      this.query();
    }
  }

  //分页
  paginate(event) {
    this.rows = event.rows;
    //每页显示的条数
    this.businessFieldManageBean.pageSize = Number(event.rows);
    //当前页
    this.currentPage = event.page + 1;
    this.businessFieldManageBean.pageNum = this.currentPage;
    this.first = event.rows * event.page;
    //调用查询的方法
    this.query();
  }
}
