import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../../../common/http/common.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { FieldDomainManageBean } from '../bean/field-domain-manage.bean';

@Component({
  selector: 'app-field-domain-update',
  templateUrl: './field-domain-update.component.html',
  styleUrls: ['./field-domain-update.component.scss']
})
export class FieldDomainUpdateComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  fieldDomainManageBean: FieldDomainManageBean = new FieldDomainManageBean;
  tableData: any[] = [];//表格数据
  code: any[];//码值
  statusId: any[] = [];//状态
  userform: FormGroup; //校验
  commonForm: FormGroup;
  //提示信息
  msgs: Message[] = [];
  contentTypeId: any[] = [];//字段类型
  ziduanyu: string;
  contentPurposeType:any=[];
  contentPurposeTypeStatus:any=[];
  constructor(
    public fb: FormBuilder,
    public commfunc: Commfunc,
    public commonHttpService: CommonHttpService
  ) {
    this.contentPurposeType = [
      { label: '请选择',     value: '' },
      { label: '前端展示',   value: '0' }
    ],
    this.contentPurposeTypeStatus = [
      { label: '请选择', value: '' },
      { label: '有效',   value: '0' },
      { label: '无效',   value: '1' }
    ]
   }

  ngOnInit() {
    this.userform = this.fb.group({
      'contentPurposeTypeName': new FormControl('', [Validators.required]),
      'contentPurposeType': new FormControl('', [Validators.required]),
      'contentPurposeTypeStatus': new FormControl('', [Validators.required]),
      'contentPurposeTypeId': new FormControl('', [Validators.required]),
    });
    this.fieldDomainManageBean.contentPurposeTypeName = this.inValue.dataResourceName;
    this.fieldDomainManageBean.contentPurposeType = this.inValue.dataResourceTypeId;
    this.fieldDomainManageBean.contentPurposeTypeStatus = this.inValue.statusId;
    this.fieldDomainManageBean.contentPurposeTypeId = this.inValue.dataResourceId;
    // console.log(this.fieldDomainManageBean)
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    this.commonHttpService.updateSpContentPurposeType(this.fieldDomainManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(
          {
            display: false,
            message: '修改成功'
          }
        )
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  modifyCloseBtn() {
    this.outValue.emit({ display: false })
  }
}
