import { Component, OnInit } from '@angular/core';
import { FieldDomainManageBean } from './bean/field-domain-manage.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { CommonHttpService } from '../../../../common/http/common.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-field-domain-manage',
  templateUrl: './field-domain-manage.component.html',
  styleUrls: ['./field-domain-manage.component.scss'],
  providers: [ConfirmationService, Commfunc, CommonHttpService]
})
export class FieldDomainManageComponent implements OnInit {
  fieldDomainManageBean: FieldDomainManageBean = new FieldDomainManageBean;
  contentPurposeTypeStatus: any = [];//业务字段域状态
  contentPurposeType: any = [];//业务字段域归属系统
  tableData: any = [];
  contentTypeId:Array<any>=[];//字段类型
  msgs: Message[] = [];//提示信息
  first: number = 0;//分页控制
  code: any;
  rows: number;//当前页条数
  currentPage: number;//当前页
  headerTitle: string;//模态框标题
  display: boolean;//模态框显示
  updateValue: any;//修改回显内容
  showModel: string;
  total: string;
  constructor(
    public commonHttpService: CommonHttpService,
    public commfunc: Commfunc,  
    public confirmationService: ConfirmationService,
  ) {
    // this.codeValues()
      this.contentPurposeType = [
      { label: '请选择',     value: '' },
      { label: '前端展示',   value: '0' }
    ],
    this.contentPurposeTypeStatus = [
      { label: '请选择', value: '' },
      { label: '有效',   value: '0' },
      { label: '无效',   value: '1' }
    ]
  }

  ngOnInit() {
    this.fieldDomainManageBean.pageNum = 1;
    this.fieldDomainManageBean.pageSize = 10;
  }

  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //查询
  query() {
    this.commonHttpService.querySpContentPurposeType(this.fieldDomainManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.tableData = data.contentPurposeTypeList;
        this.total=data.totalCount;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //点击查询
  queryClick() {
    this.first = 0;
    this.fieldDomainManageBean.pageNum = 1;
    this.query();
  }

  //重置
  resetClick() {
    this.fieldDomainManageBean.contentPurposeTypeName = '';
    this.fieldDomainManageBean.contentPurposeType = '';
    this.fieldDomainManageBean.contentPurposeTypeStatus = '';
  }

  //新增
  add() {
    this.headerTitle = "业务字段域新增";
    this.showModel = "1";
    this.display = true;
  }

  //新增回调
  addCall(param) {
    this.display = param.display;
    if (param.message) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.message })
    }
    this.query();
  }
  //删除
  del(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        // 条件
        let param = { contentPurposeTypeId: item.dataResourceId };
        this.commonHttpService.removeSpContentPurposeType(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  update(item) {
    this.headerTitle = "业务字段域修改";
    this.showModel = "2";
    this.display = true;
    this.updateValue = item;
  }
  //修改回调
  updateCall(param) {
    this.display = param.display;
    if (param.message) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.message })
    }
    this.query();
  }

  //分页
  paginate(event) {
    this.rows = event.rows;
    //每页显示的条数
    this.fieldDomainManageBean.pageSize = Number(event.rows);
    //当前页
    this.currentPage = event.page + 1;
    this.fieldDomainManageBean.pageNum = this.currentPage;
    this.first = event.rows * event.page;
    //调用查询的方法
    this.query();
  }

}
