import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BusinessFieldComponent } from './business-field.component';
import { BusinessFieldManageComponent } from './business-field-manage/business-field-manage.component';
import { FieldDomainManageComponent } from './field-domain-manage/field-domain-manage.component';

const routes: Routes = [
  {
    path: '',
    component: BusinessFieldComponent,
    children: [
        {
            path: 'business-field-manage', component: BusinessFieldManageComponent,
        },
        {
            path: 'field-domain-manage', component: FieldDomainManageComponent,
        },
    ]
},
];

export const BusinessFieldRouting:ModuleWithProviders = RouterModule.forChild(routes);
