import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../../../common/http/common.http.service';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { BusinessFieldManageBean } from '../bean/business-field-manage.bean';

@Component({
  selector: 'app-field-update',
  templateUrl: './field-update.component.html',
  styleUrls: ['./field-update.component.scss']
})
export class FieldUpdateComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  providers: [Commfunc]
  businessFieldManageBean: BusinessFieldManageBean = new BusinessFieldManageBean;
  tableData: any[] = [];//表格数据
  code: any[];//码值
  statusId: any[] = [];//状态
  userform: FormGroup; //校验
  contentPurposeTypeId: string;
  contentPurposeTypeIdOpt: Array<any>;
  commonForm: FormGroup;
  //提示信息
  msgs: Message[] = [];
  ziduanyu: string;
  constructor(
    public fb: FormBuilder,
    public commfunc: Commfunc,
    public commonHttpService: CommonHttpService
  ) {
    this.codeValues()
    this.statusId = this.code["SpContentStatus"];
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'contentPurposeTypeId': new FormControl('', [Validators.required]),
      'contentId': new FormControl('', [ Validators.required]),
      'contentName': new FormControl('', [ Validators.required]),
      // 'contentTypeId': new FormControl('', [Validators.required]),
      'statusId': new FormControl('', [Validators.required]),
    });

    //字段域关联方
    let param = { }
    this.commonHttpService.queryAllSpContentPurposeType(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let contentPurposeTypeIdOpt = this.commfunc.listToLabelvalue(data.contentPurposeTypeList, "dataResourceName", "dataResourceId")
        contentPurposeTypeIdOpt.unshift({ label: "请选择", value: '' });
        this.contentPurposeTypeIdOpt = contentPurposeTypeIdOpt
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

    this.businessFieldManageBean = this.inValue;
    
    
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  preserveClick() {
    
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    this.commonHttpService.updateSpContent(this.businessFieldManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.tableData = data
        this.outValue.emit({ display: false, message: data.returnCode.message })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  
  modifyCloseBtn() {
    this.outValue.emit({ display: false })
  }
}
